.App {
  text-align: center;
  padding: 20px;
}

.game-container {
  max-width: 800px;
  margin: 0 auto;
}

.game-board {
  width: 500px;
  height: 500px;
  background-color: #b3e0ff;
  position: relative;
  margin: 20px auto;
  border: 2px solid #333;
  overflow: hidden;
}

.game-info {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f0f0f0;
  border-radius: 5px;
  margin-bottom: 10px;
}

.game-object {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  position: absolute;
  transition: all 0.3s ease;
}

.hook-container {
  position: absolute;
  width: 30px;
  height: 30px;
  z-index: 100;
}

.fishing-line {
  position: absolute;
  background-color: #666;
  width: 2px;
  transform-origin: top center;
}

.hook {
  position: absolute;
  font-size: 24px;
  transform-origin: top center;
  cursor: pointer;
}

.catch-button {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1000;
}

.catch-button:hover {
  background-color: #45a049;
}

.start-button {
  padding: 15px 30px;
  font-size: 20px;
  background-color: #2196F3;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.start-button:hover {
  background-color: #1976D2;
}

.score-board, .timer {
  font-size: 20px;
  font-weight: bold;
} 